:root {
    --backgroundColor: 0;
}

.circleContainer {
    display: grid;
    align-self: center;
    justify-content: center;
}

.circle {
    display: grid;
    grid-area: Area1;
    width: 400px;
    height: 400px;
    background-color: var(--backgroundColor);
    border-radius: 50%;
    border: 2px solid;
}

.text {
    align-self: center;
    font-family: Arial;
    text-align: center;
    font-size: 25px;
    font-weight: bold;
}
